@keyframes ldio-of4mr6z053l {
    0% {
        transform: rotate(0)
    }

    100% {
        transform: rotate(360deg)
    }
}

.loading-wrapper {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ldio-of4mr6z053l div {
    box-sizing: border-box !important
}

.ldio-of4mr6z053l>div {
    position: absolute;
    width: 144px;
    height: 144px;
    top: 28px;
    left: 28px;
    border-radius: 50%;
    border: 16px solid #000;
    border-color: #ffffff transparent #ffffff transparent;
    animation: ldio-of4mr6z053l 1s linear infinite;
}

.ldio-of4mr6z053l>div:nth-child(2) {
    border-color: transparent
}

.ldio-of4mr6z053l>div:nth-child(2) div {
    position: absolute;
    width: 100%;
    height: 100%;
    transform: rotate(45deg);
}

.ldio-of4mr6z053l>div:nth-child(2) div:before,
.ldio-of4mr6z053l>div:nth-child(2) div:after {
    content: "";
    display: block;
    position: absolute;
    width: 16px;
    height: 16px;
    top: -16px;
    left: 48px;
    background: #ffffff;
    border-radius: 50%;
    box-shadow: 0 128px 0 0 #ffffff;
}

.ldio-of4mr6z053l>div:nth-child(2) div:after {
    left: -16px;
    top: 48px;
    box-shadow: 128px 0 0 0 #ffffff;
}

.loadingio-spinner-dual-ring-drwpaa1s9x6 {
    width: 200px;
    height: 200px;
    display: inline-block;
    overflow: hidden;
    background: none;
}

.ldio-of4mr6z053l {
    width: 100%;
    height: 100%;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0;
    /* see note above */
}

.ldio-of4mr6z053l div {
    box-sizing: content-box;
}